.businessContainer {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
}

.mainContainer {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.circularInfoContainer {
  height: 70%;
  display: flex;
  flex-flow: column;
  align-items: center;
}

.lowestMaterialinfoContainer {
  padding-top: 10px;
  display: flex;
  width: 100%;
}

.lowestMaterialinfoContainer > svg {
  margin-left: 10px;
}

.pMaterialContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: space-around;
}

.pChildContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.pChildContainer > p {
  margin: 0;
}

.mainTitle {
  display: flex;
  width: 100%;
  height: 30%;
  justify-content: center;
  align-items: center;
}

.infoContainer {
  width: 100%;
  height: 50%;
  display: flex;
  margin-top: 20px;
  flex-direction: column;
}

h1 {
  font-family: 'Montserrat-Bold', sans-serif;
}

h2 {
  color: var(--green);
}
